.footer {
    background: linear-gradient(to bottom, rgba(233, 193, 180, 0.1), #e9c1b4 60%, #c59882 80%);
    color: white;
    padding: 10px 0;
    text-align: center;
}
.container-footer {
    top: 40px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 80%;
    margin: 0 auto;
}

.social-media{
    padding-left: 10px;
}
.policy,
.more-info,
.products,
.location {
    margin: 1px 0;
}



.product-list {
    list-style-type: none;
    padding: 0;
}

.product-image {
    width: 40px;
    height: 40px;
    margin-right: 5px;
}
