.anuncio-mantenimiento-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(10px);
  z-index: 2000;
}

.anuncio-mantenimiento-modal {
  background-color: #1f1d1d96;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 400px;
  
}

.texto-mantenimiento {
  font-size: 18px;
  color: #fff6f6;
  margin-bottom: 15px;
}

.boton-mantenimiento {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #ff5622a9;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.boton-mantenimiento:hover {
  background-color: #ff149157;
}
