
.calendar-container {
  font-family: 'Arial', sans-serif;
  background: linear-gradient(to bottom, #e9c39d 10%, #e9c1b4 60%, #eceae9 80%);
  padding: 20px;
  max-width: 600px;
  margin: 0 auto;
  margin-top: 70px;
  text-align: center;
  color: #333;
  border: saddlebrown 2px solid;
  box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.2);
}

/* Encabezado del calendario */
.calendar-container h2 {
  font-size: 24px;
  margin-bottom: 20px;
  color: #ff69b4;
}

/* Estilo para la cuadrícula del calendario */
.calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 10px;
}

/* Estilo para cada día del calendario */
.day {
  background-color: #ddd;
  border-radius: 50%;
  width: 70px;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  cursor: pointer; 
}

.day.important {
  background-color: #ff69b4; /* Color destacado para días importantes */
}

.day span {
  font-size: 20px;
  color: #333;
}

/* Estilo para la etiqueta de días importantes */
.day .label {
  position: absolute;
  bottom: -15px;
  font-size: 12px;
  width: 100%;
  color: #333;
}

/* Botones de navegación */
.navigation-buttons {
  margin-top: 20px;
}

.navigation-buttons button {
  background-color: #ff69b4;
  color: white;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
}

.navigation-buttons button:hover {
  background-color: #ff4081;
}

/* Lista de fechas importantes */
.important-dates-list {
  margin-top: 30px;
}

.important-dates-list h3 {
  font-size: 20px;
  color: #ff69b4;
}

.important-dates-list ul {
  list-style-type: none;
  padding: 0;
  font-size: 16px;
}

.important-dates-list li {
  margin: 5px 0;
  color: #333;
}

/* Consultas de medios para dispositivos móviles */
@media (max-width: 768px) {
  .calendar-container {
    max-width: 80%;
    margin-bottom: 100%;
    margin-top: 70px;
    border-radius: 30px;
  }

  .calendar-container h2 {
    font-size: 20px;
  }

  .day {
    width: 30px;
    height: 30px;
    font-size: 12px;
  }

  .day span {
    font-size: 16px;
  }

  .navigation-buttons button {
    padding: 8px 16px;
    font-size: 14px;
  }

  .important-dates-list h3 {
    font-size: 13px;
  }

  .important-dates-list ul {
    font-size: 13px;
  }
}
