.favoritos-container {
    margin-top: 40px;
    padding: 20px;
    height: auto; /* Change height to auto for infinite height */
}

h2 {
    text-align: center;
    margin-bottom: 20px;
    font-size: 24px;
}

.favoritos-list {
    list-style: none;
    padding: 0;
    display: flex;
    flex-direction: column;
    gap: 15px;
} 

.favorito-item {
    display: flex;
    align-items: center;
    border: 1px solid #ddd;
    padding: 10px;
    border-radius: 8px;
    background-color: #f9f9f9;
    transition: box-shadow 0.3s ease;
    
}

.favorito-item:hover {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transform: scale(1.01);
    cursor: pointer;
    background-color: #c4c4c4;
    border-radius: 8px;
    border: 1px solid #272727;

}

.favorito-img {
    width: 80px;
    height: 80px;
    object-fit: cover;
    margin-right: 20px;
    border-radius: 8px;
    cursor: pointer;
}

.favorito-info {
    flex-grow: 1;
}

.favorito-info h3 {
    margin: 0 0 10px;
    font-size: 20px;
}

.favorito-info p {
    margin: 5px 0;
    font-size: 16px;
}

.favorito-actions {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.ver-producto-btn, .eliminar-favorito-btn {
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.eliminar-favorito-btn {
    background-color: #e74c3c;
    color: white;
}

.eliminar-favorito-btn:hover {
    background-color: #c0392b;
}
