.flor-benefits {
    position: relative;
    overflow: hidden;
    margin-top: 0%;
    padding: 20px;
    border: 8px;
}

.flor-grid {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: nowrap;
    transition: transform 0.3s ease;
    will-change: transform;
    border-color: #000000;
}

.flor-card {
    width: 30%;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    text-align: center;
    margin: 0 10px;
    border: 1px solid;
    transition: opacity 0.5s ease;
}

.flor-image {
    margin-top: 4px;
    width: 97%;
    height: 300px;
}

.flor-info {
    padding: 10px;
}

.flor-info p {
    margin: 5px 0;
    font-size: 14px;
    color: #333;
}

.whatsapp-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    color: rgb(0, 126, 52);
    border: none;
    border-radius: 50%;
    padding: 10px;
    font-size: 30px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    margin-top: -10px;
    margin-left: 41%;
}

.whatsapp-btn:hover {
    background-color: #1ebe5b;
}

.whatsapp-btn svg {
    font-size: 30px;
}


@media (max-width: 768px) {
    .flor-benefits{
    margin-top: -20%;
    
    }
    .flor-grid {
        display: flex;
        flex-direction: row;
        overflow-x: hidden;
    }

    .flor-card {
        display: none; 
        width: 95%;
        flex-shrink: 0;
        margin: 0 10px;
        height: 430px;
    }

    .flor-card.active {
        display: block; 
    }

    .anterior-btn,
    .siguiente-btn {
        display: block;
        position: absolute;
        top: 50%;
        font-size: 16px;
        background-color: rgba(146, 146, 146, 0.295);
        border: none;
        cursor: pointer;
        z-index: 1;
        width: 30px;
        height: 30px;
        border-radius: 50%;
    }

    .siguiente-btn {
        right: 10px;
        color: #0f0f0f;
    }

    .anterior-btn {
        left: 10px;
        color: #000000;
    }
}

@media (min-width: 769px) {
    .flor-grid {
        justify-content: space-between;
        flex-direction: row;
    }

    .flor-card {
        width: 30%;
        margin: 0 10px;
        display: block; 
    }

    .anterior-btn,
    .siguiente-btn {
        display: none; 
    }
}
