.spinner-container {
  position: fixed; 
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(3, 3, 3); /* Fondo semi-transparente */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 1000; 
}

.spinner {
  border: 3px solid rgba(0, 0, 0, 0.1); /* Fondo del spinner */
  border-left: 4px solid #6d776f; /* Color de la parte giratoria */
  border-radius: 50%;
  width: 30px;
  height: 30px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.spinner-dots {
  display: flex;
  margin-top: 10px;
}

.spinner-dots div {
  width: 6px;
  height: 6px;
  background-color: #a7a5a5;
  border-radius: 50%;
  margin: 0 2px;
  opacity: 0;
  animation: blink 1.5s linear infinite;
}

.spinner-dots div:nth-child(1) {
  animation-delay: 0s;
}

.spinner-dots div:nth-child(2) {
  animation-delay: 0.2s;
}

.spinner-dots div:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes blink {
  0%, 100% { opacity: 0; }
  50% { opacity: 1; }
}

.animated-text {
  font-size: 30px;
  color: #03b41b;
  margin-top: 40px;
  display: flex;
  gap: 18px;
}

.animated-text span {
  opacity: 0;
  animation: fadeInLetter 1.5s forwards;
  animation-delay: calc(var(--i) * 0.1s);
}

@keyframes fadeInLetter {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

.spinner-container p {
  margin-top: 10px;
  font-size: 10px;
  color: #a7a5a5;
}
