.tooltip2 {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 10px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 100;
  width: 50%;
  height: 50vh;
  left: 50%;
  transform: translateX(-50%);
  transition: transform 0.3s ease-out;
  overflow: hidden;
  touch-action: none;
}

.tooltip2.expanded {
  height: 60vh;
  max-height: 85vh;
}

.tooltip2 p {
  width: 100%;
  text-align: center;
}

.tooltip-image {
  width: 50px;
  height: 50px;
  object-fit: cover;
  margin-bottom: 10px;
}

.tooltip-close {
  position: absolute;
  top: 5px;
  left: 5px;
  color: rgb(0, 0, 0);
  background-color: transparent;
  border: none;
  font-size: 26px;
  cursor: pointer;
  border-radius: 50px;
  z-index: 101;
}

.tooltip-close:hover {
  color: red;
}

.tooltip-bar {
  display: none;
}

.tooltip-content {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  padding: 20px 10px;
  box-sizing: border-box;
}

@media (max-width: 768px) {
  .tooltip2 {
      background-color: rgb(255, 255, 255);
      padding: 10px;
      border-radius: 20px 20px 0 0;
      margin-left: -1px;
      width: 90%;
      transform: translateX(-50%);
      will-change: transform;
      position: fixed;
      bottom: 0;
  }

  .tooltip-bar {
      display: block;
      width: 60px;
      height: 5px;
      background-color: rgb(136, 136, 136);
      border-radius: 3px;
      cursor: pointer;
      margin: 5px auto 15px;
  }
}