.navbar{
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
  border-radius: 0 0 30px 30px;

}

.nav_logo {
  position: relative;
  font-weight: bold;
  font-size: 18px;
  margin: 0;
  padding-left: -10px;
  padding-right: 80px;
  margin-left: 20px;
}

@media (max-width: 768px) {
  .nav-corazon {
    position: absolute;
    width: 12px;
    margin-bottom: 4px;
    margin-left: 78%;
    cursor: pointer;
  }
}

@media (min-width: 769px) {
  .nav-corazon {
    display: none;
  }
}

.nav-corazon:hover {
  color: red;
}

.logo-image {
  height: 40px; 
  width: 40px; 
  border-radius: 50%;
  margin-right: -60px;
  margin-top: 5px;
  margin-left: 50px;
  z-index: 1;
  position: relative;
  padding: -10px;
}

.logo-image:hover {
  cursor: pointer;
  transform: scale(1.18);
}

.nav_items a {
  position: relative; /* Asegura que el tooltip se posicione correctamente en relación con el enlace */
  margin: 0;
  color: rgb(255, 255, 255);
  text-decoration: none;
  font-size: 15px;
  margin-top: 10px;
  padding: 15px;
  border-radius: 30px;
  transition: background-color 0.3s, color 0.3s;
}

.nav_items a:hover {
  background-color: #ff149157;
  color: #fff;
}

.inicio-active {
  background-color: #e7cdb3;
  border-radius: 30px;
  color: rgb(255, 255, 255);
}

.nav_toggle {
  display: none;
}

@media (max-width: 768px) {
  .nav_items {
    position: absolute;
    top: 60px; /* Ajusta el espacio desde la parte superior para que no se superponga con la barra fija */
    left: 0;
    background: linear-gradient(to bottom,  10%, #e9c1b4 60%, #eceae9 80%);
    display: flex;
    flex-direction: column;
    width: 100%;
    height: calc(100vh - 60px); /* Ajusta la altura considerando la altura de la barra fija */
    transform: translateX(-100%);
    transition: 0.3s ease all;
    cursor: pointer;
    border-radius: 30px;
    color: red;
  }
  

  .nav_items.open {
    transform: translateX(0) !important;
  }

  .nav_toggle {
    display: flex !important;
    flex-direction: column;
    margin: 10px;
  }

  .nav_toggle span {
    width: 0;
    height: 3px;
    background: rgb(8, 8, 8);
    margin-bottom: 4px;
    border-radius: 2px;
    transform-origin: 5px 0;
    transition: all 0.2s linear;
    margin-right: 10px;
    cursor: pointer;
  }

  .nav_toggle.open > span {
    transform: rotate(45deg) translate(0, 3px);
  }

  .nav_toggle.open > span:nth-child(2) {
    display: none;
  }

  .nav_toggle.open > span:nth-child(3) {
    transform: rotate(-45deg) translate(-5px, -2px);
  }
}



.auth-buttons {
  display: none;
}

@media (max-width: 700px) {
  .auth-buttons {
    display: contents;
    align-items: center;
    font-size: 15px;
  }

  .auth-btn {
    position: absolute;
    left: 25%;
    font-size: 25px;
    background-color: #35363581;
    color: #fffcfc;
    width: 30%;
    text-decoration: none;
    border-radius: 30px;
    transition: background-color 0.3s, color 0.3s;
  }

  .auth-btn:hover {
    background-color: #ff149157;
    color: #fff;
  }

  .social-auth {
    position: absolute;
    display: flex;
    align-items: center;
    margin-left: 30%;
  }

  .social-btn {
    color: #faf2f2;
    font-size: 20px;
    text-decoration: none;
  }

  .social-btn:hover {
    color: #ff149157;
  }
}

