.container {
    
    margin-top: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    margin-bottom: 80px;
    background-color: #22252700; /* Un color de fondo suave */
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
  
  h1 {
    font-size: 0.90rem;
    color: #000000; /* Un color llamativo para el texto */
    text-align: center;
    transition: opacity 1s ease-in-out;
    opacity: 0;
    text-transform: uppercase;
    text-shadow: 1px 2px 4px rgba(0, 0, 0, 0.5);




  }
  
  .container h1.show {
    opacity: 1;
  }
  
  .flower-image {
    position: absolute;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-top: 80px;
    animation: rotate 10s linear infinite;
    animation-name: shake;
    animation-duration: 0.5s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
    box-shadow: 0 0px 5px rgba(0, 0, 0, 0.5);
    cursor: pointer;
    z-index: 2; /* Imagen detrás */
}

.r {
    position: fixed;
    top: 50%;  /* Centra verticalmente */
    left: 50%;  /* Centra horizontalmente */
    transform: translate(-50%, -50%);  /* Asegura que el centro sea exacto */
    animation: rotate 10s linear infinite;
    animation-name: shake;
    animation-duration: 0.5s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
    color: aliceblue;

    opacity: 1;
    z-index: 5;  /* Mayor que el z-index de .flower-image para estar delante */
}

#animated-text {
  opacity: 1;
  transition: opacity 1s ease-in-out; /* Transición de opacidad */
}

.fade-out {
  opacity: 0; /* Oculta el texto */
}

.fade-in {
  opacity: 1; /* Muestra el texto */
}
