.search-bar {
  display: flex;
  justify-content: center;
  margin: 80px 0;
  position: relative;
  margin-bottom: -50px;
  z-index: 1;
}
/* Lista de sugerencias */
.suggestions-list {
  position: absolute;
  top: 70%; /* Aparece justo debajo del input */
  left: 25%;
  right: 0;
  height:auto;
  max-height: 200px;
  width: 46%;
  overflow-y: auto; /* Permitir desplazamiento vertical */
  background-color: white;
  border: 1px solid #ddd;
  border-radius: 0 0 10px 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  z-index: 1;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer y Edge */
}
.suggestions-list::-webkit-scrollbar {
  display: none; /* Chrome, Safari, Opera */
}


.search-bar form {
  display: flex;
  width: 100%;
  max-width: 600px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 25px;
  overflow: hidden;
}

.search-bar input {
  flex-grow: 1;
  padding: 10px 15px;
  font-size: 16px;
  border: none;
  outline: none;
}

.search-bar button {
  background-color: #d4a373;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.search-bar button:hover {
  background-color: #ff149157;
}

.search-bar button svg {
  width: 20px;
  height: 20px;
}


.suggestions-list li {
  display: flex;
  align-items: center;
  padding: 10px 15px;
  cursor: pointer;
  margin-left:-40px;
  transition: background-color 0.3s ease;
}

.suggestions-list li:hover {
  background-color: #f0f0f0;
  
}

.suggestions-list li img {
  width: 40px; 
  height: 40px;
  border-radius: 50%; 
  margin-right: 25px; 
}

/* Ajustes responsivos */
@media (max-width: 600px) {
  .search-bar form {
    max-width: 90%;
  }

  .suggestions-list {
    max-height: 150px;
  }
  .suggestions-list{
    margin-left: -60px;
    width: 70%;
    
  }
}
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7); /* Fondo oscuro con transparencia */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* Asegúrate de que esté encima de otros elementos */
  backdrop-filter: blur(5px); /* Aplica un desenfoque de 5px al fondo */
  width: 100%;
}

.modal-content {
  background: white;
  border-radius: 8px;
  margin-top: 12%;
  padding: 20px; /* Ancho máximo del modal */
  width: 95%; /* Ancho del modal */
  max-height: 80%; /* Máxima altura del modal */
  overflow-y: auto; /* Permitir desplazamiento vertical si hay demasiado contenido */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Sombra para el modal */
}

 .modal-content .xun {
  position: fixed;
  background-color: #0f0e0e73;
  color: rgb(255, 255, 255);
  border: none;
  width: 30px;
  height: 30px;
  margin-left: 30%;
  margin-top: 10%;
  border-radius: 50%;
  padding: 2px 5px;
  cursor: pointer; /* Cambiado a 'hidden' para ocultar la barra de desplazamiento */
}


.product-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3 columnas */
  gap: 10px; /* Espacio entre elementos */
}

.product-card img {
  width: 60px; /* Cambia este valor para hacer la imagen más pequeña */
  height: 60px; /* Asegúrate de que la altura esté sincronizada con el ancho */
  border-radius: 5px; /* Opcional: mantener bordes redondeados */
  margin-bottom: 10px; /* Espacio entre la imagen y el texto */
}

.product-card:hover {
  background: #cbf3e48a; /* Color de fondo al pasar el ratón */
  cursor: pointer; /* Cambia el cursor al pasar el ratón */
}
