.ver-producto-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding:0;
  background-color: aliceblue;
  position: relative; 
  margin-top: 0px;
}

.producto-card {
  width: 100%;
  margin-top: 42px;
  background: linear-gradient(to bottom, #e0c1a2 10%, #fffbf9 60%, #eceae9 80%);
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.volver {
  position: absolute;
  top: 50px;
  left: 10px;
}

.volver-btn {
  position: fixed;
  background: #6b696934;
  border: none;
  cursor: pointer;
  padding: 10px; /* Ajusta el padding si es necesario */
  border-radius: 50%;
  transition: background-color 0.3s;
}

.icono{
  width: 20px;
  height: 20px;
  color: #0c0b0b;  
}

.iconos-3 {
  width: 24px;
  height: 24px;
  color: #666;
}

.producto-imagen {
  width: 95%;
  height: auto;
  max-height: 350px;
  padding: 10px 10px 10px 10px;
  padding-top: 5%;
  object-fit: cover;
  border-top-left-radius: 30px;
  border-top-right-radius:30px ;
}

.producto-contenido {
  padding: 20px;
}

.producto-encabezado {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.producto-titulo {
  text-align:left;
  font-size: 1.5rem;
  font-weight: bold;

}

.producto-acciones {
  display: flex;
  gap: 10px;
}

.accion-btn {
  background: none;
  border: none;
  cursor: pointer;
  padding: 5px;
  border-radius: 50%;
  transition: background-color 0.3s;
  color: black;
}

.accion-btn:hover {
  background-color: #dfbd9a00;
  transform: scale(1);
}


.icono-1.favorito {
  fill: #e53e3e;
  color: #e53e3e;
}

.producto-categoria {
  color: #666;
  margin-bottom: 10px;
}

.producto-precios-1 {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
  margin-top: -30px;
}

.precio-actual-1 {
  font-size: 2.25rem;
  font-weight: bold;
  color: #48bb78;
  
}

.precio-original-1 {
  font-size: 1.875rem;
  text-decoration: line-through;
  color: #999;
}

.producto-descripcion {
  font-size: 1.5rem;
  color: #666;
  margin-bottom: 15px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  justify-content: end;
  text-align:left;
}

.producto-opciones {
  margin-bottom: 15px;
}

.opciones-botones {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  margin-top: 20px;
  margin-bottom: 30px;
}

.opcion-btn {
  background-color: #f8bbbb;
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  font-size: 0.875rem;
  cursor: pointer;
}

.opcion-btn:hover {
  background-color: #f6a8a8;
  transform: scale(1.1);
}

.producto-compra {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cantidad-control {
  display: flex;
  align-items: center;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.cantidad-btn {
  background: none;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  font-size: 1rem;
}

.cantidad-valor {
  padding: 0 10px;
}

.agregar-btn {
  display: flex;
  align-items: center;
  gap: 5px;
  background-color: #48bb78;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 4px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s;
}

.agregar-btn:hover {
  background-color: #38a169;
}

.icono2 {
  width: 40px;
  height: 30px;
}
