.carousel-container {
  overflow-x: scroll;
  overflow-y: hidden;
  width: 100%;
  white-space: nowrap;
  scroll-snap-type: x mandatory;
  -ms-overflow-style: none;
  scrollbar-width: none;
  margin-bottom: -30px;
  margin-top: 90px;
  margin-left: 3px;
  position: relative;
}
.carousel-container::-webkit-scrollbar {
  display: none;
}

.carousel {
  display: flex;
  margin-left: 10px;
  display: flex;
  z-index: 1;
}

.producto {
  min-width: 220px;
  margin-right: 10px;
  margin-left: 10px;
  scroll-snap-align: start;
}


@media (max-width: 768px) {
  .producto {
    min-width: 120px;
  }
  .carousel-container {
    height: 220px;
    z-index: 2;
    margin-top: 80px;
  }
}

.ver-producto-btn {
  position: relative;
  display: inline-block;
  margin-left: 28px;
  padding: 10px 10px;
  margin-top: inline-block;
  background-color: #d4a373;
  color: #fff;
  text-align: center;
  border-radius: 5px;
  border-color: #d4a373;
  font-weight: bold;
  transition: background-color 0.3s, transform 0.3s;
}

.corazon-btn {
  background-color: #79797900;
  width: 50px;
  height: 50px;
  padding: 10px;
  margin-bottom: 0px;
  color: #d4a373;
  border-radius: 5px;
  border: #07070700;
  font-size: 25px;
  margin-top:-10px; 
  margin-left: 20px;
}

.corazon-btn:hover {
  transform: scale(1.5);
  cursor: pointer;
}
@media (max-width: 768px) {
  .corazon-btn{
    width: 40px;
    height: 40px;
    padding: 1px;
    margin-bottom: 10px;
    margin-left: 70px;
    color: #f8ebdf;
    border-radius: 5px;
    border: #d4a373;
    font-size: 25px;
    margin-top: -30px;
  }
}

.ver-producto-btn:hover {
  background-color: #ff149157;
  transform: scale(1.1);
  cursor: pointer;
}

.productos, .related-products {
  margin-top: 10px;
  margin-left: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.producto {
  background-color: #f8f4f0;
  border: 0.5px solid #beb3a8;
  padding: 10px;
  margin: 10px;
  width: 220px;
  text-align: left;
  margin-left: 10px;
  transition: transform 0.3s, box-shadow 0.3s;
  cursor: pointer;
  overflow: hidden;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.753);
  border-radius:30px;
  cursor: pointer;
}

.producto:hover,
.producto:focus {
  transform: scale(0.95);
  box-shadow: 0 0 13px #09b403;
  background-color: #c1cfc4;
  font-size: 1.001em;
 
}
.producto img {
  width: 200px;
  height: 200px;
  display: block;
  margin: 0 auto;
  box-shadow: 0 0 30px rgb(167, 148, 148);
  border-radius: 30px;
}

.producto img:hover {
  transform: scale(1.01);
}

.producto h3, .producto h4 {
  margin: 10px 0 0;
  font-size: 1.3em;
}

.producto p {
  margin: 10px 0 0;
}

.precio-original {
  text-decoration: line-through;
  color: #a0a0a0;
  margin-left: 30px;
  font-size: 20px;
}

.precio-descuento {
  color: #d4a373;
  font-weight: bold;
  font-size: 24px;
  margin-left: 5px;
}

.product-details {
  margin: 20px;
  text-align: center;
  position: reltive;
}

.product-main img {
  width: 400px;
  max-width: 350px;
  height: 350px;
  background-color: #f8f4f0;
  border: 0.5px solid #070707;
  padding: 0px;
  margin: 10px;
  text-align: left;
  transition: transform 0.3s, box-shadow 0.3s;
  cursor: pointer;
  overflow: hidden;
  box-shadow: 0 0 5px rgb(0, 0, 0);
  border-radius: 30px;
}

a {
  text-decoration: none;
  color: inherit;
}

@media (max-width: 768px) {
  .ver-producto-btn {
    padding: 4px 0px 5px 0px;
    font-size: 0.8em;
    width: 70px;
    height: 30px;
    margin-left: -30px;
    font-weight: bold;
    white-space: nowrap;      /* Mantener el texto en una sola línea */
    overflow: hidden;         /* Ocultar el texto que excede el ancho */
    text-overflow: ellipsis;  /* Añadir puntos suspensivos (...) al final */
    border-bottom: #d4a373;
    transform: scale(0.8);
}

  
  .ver-producto-btn:hover {
    background-color: #ff149157;
    transform: scale(0.9);
    cursor: pointer;
  }
  .caro-btn {
    width: 30px;
    height: 30px;
    padding: 1px;
    margin-bottom: 10px;
    margin-left: 60px;
    color: #d4a373;
    border-radius: 5px;
    border: #07070700;
    font-size: 20px;
    margin-top: -30px;
  }

  .productos, .related-products {
    flex-direction: row;
    margin-top: 60px;
    align-items: center;
    justify-content:left;
    padding: 5px;
    flex-wrap: wrap;
    z-index: 1;
  }

  .producto {
    width: 100px;
    padding: 5px;
    margin: 5px;
    max-width: 120px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 12px;
  }
  .producto:hover{
    font-size: 12px;
  }

  .producto img {
    width: 90px;
    height: 90px;
    border-radius: 15px;
    box-shadow: none;
  }

  .producto h3, .producto h4 {
    margin: 5px 0 -13px;
    text-align: center;
  }

  .producto p {
    font-size: 0.8em;
    text-align: center;
  }

  .precio-original {
    font-size: 1.1em;
    margin-left: 1px;
  }

  .precio-descuento {
    font-size: 1.3em;
    
  }

  .product-details {
    font-size: 0.9em;
    margin: 10px;
  }

  .product-main img {
    width: 200px;
    max-width: 200px;
    height: auto;
    border-radius: 15px;
    box-shadow: none;
  }

  .nav_icon {
    font-size: 14px;
  }

}

.nav_icon {
  margin-right: 8px;
  vertical-align: middle;
  font-size: 18px;
