.contact {
  background: #d4a373;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  flex-direction: column;
}

.contact-container {
  background-color: #ecf0f3;
  margin: 30px;
  margin-top: 80px;
  margin-left: 20%;
  max-width: 600px;
  max-width: 1200px;
  padding: 50px;
  width: 60%;
  height: 80%;
  border: 3px solid #d18e4b;
  border-radius: 40px;
  box-shadow: 0 0 40px rgb(0, 0, 0);
}

.contact-info {
  margin-bottom: 20px;
  justify-content: center;
}

.contact-info h3 {
  margin-bottom: 5px;
}

.contact-info p {
  margin: 0;
}

.social-media {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.social-media a {
  color: #000;
  /* Color por defecto de los íconos */
  text-decoration: none;
  padding: 20px;
  font-size: 24px;
}

.social-facebook-media a:hover {
  color: #007bff;
  /* Cambia el color al pasar el mouse */
  transform: scale(0.95);
}

.social-instagram-media a:hover {
  color: #e91616;
  /* Cambia el color al pasar el mouse */
  transform: scale(0.95);
}

.social-whatsaap-media a:hover {
  color: #409c5f;
  /* Cambia el color al pasar el mouse */
  transform: scale(0.95);
}

.contact-form form {
  display: flex;
  flex-direction: column;
}

.contact-form label {
  margin-bottom: 15px;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.contact-form button {
  padding: 10px;
  border: none;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  border-radius: 5px;
}

.contact-form button:hover {
  background-color: #0056b3;
}

/* Estilos de la barra de navegación */
.navbar {
  background: #d4a373;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
}

.nav_logo {
  position: relative;
  font-weight: bold;
  font-size: 18px;
  margin: 0px;
  padding-left: -20px;
  margin-left: -20px;
  color: rgb(2, 2, 2);
}

.logo-image {
  height: 40px;
  /* Ajusta el tamaño según lo necesites */
  width: 40px;
  /* Ajusta el tamaño según lo necesites */
  border-radius: 50%;
  margin-right: -60px;
  margin-top: 5px;
  margin-left: 50px;
  z-index: 1;
  position: relative;
  padding: -10px;
}

.logo-image:hover {
  cursor: pointer;
  transform: scale(1.18);
}

.nav_items a {
  margin: 0px;
  color: rgb(0, 0, 0);
  position: relative;
  text-decoration: none;
  font-size: 15px;
  margin-top: 10px;
  padding: 15px;
  border-radius: 30px;
  /* Ajusta el padding para que el tamaño del enlace sea constante */
  transition: background-color 0.3s, color 0.3s;
  /* Solo la transición del color de fondo y texto */
}

.nav_items a:hover {
  background-color: #ff149157;
  color: rgb(0, 0, 0);
  border-radius: 30px;
  color: #fff;
}

.nav_toggle {
  display: none;
}

@media (max-width: 768px) {
  .contact {
    background: #d4a373;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;
  }

  .contact-container {
    background-color: #ecf0f3;
    margin: 30px;
    margin-top: 70px;
    max-width: 600px;
    max-width: 1200px;
    margin-left: 19px;
    padding: 50px;
    padding-top: 20px;
    position: center;
    width: 60%;
    height: 80%;
    border: 3px solid #d18e4b;
    border-radius: 30px;
    box-shadow: 0 0 20px rgb(0, 0, 0);
  }
  .contact-container:hover {
    background-color: rgb(226, 226, 226);
  }

  .nav_items {
    position: absolute;
    top: 50px;
    /* Ajusta el espacio desde la parte superior para que no se superponga con la barra fija */
    left: 0;
    background: linear-gradient(to bottom, #d18e4b 10%, #e9c1b4 60%, #eceae9 80%);
    display: flex;
    flex-direction: column;
    width: 100%;
    height: calc(100vh - 60px);
    /* Ajusta la altura considerando la altura de la barra fija */
    transform: translateX(-100%);
    transition: 0.3s ease all;
    cursor: pointer;
  }

  .nav_items.open {
    transform: translateX(0) !important;
  }

  .nav_toggle {
    display: flex !important;
    flex-direction: column;
    margin: 10px;
    /* Reduce el margen */
  }

  .nav_toggle span {
    width: 20px;
    /* Reduce el ancho */
    height: 3px;
    /* Reduce la altura */
    background: rgb(8, 8, 8);
    margin-bottom: 4px;
    /* Reduce el margen inferior */
    border-radius: 2px;
    transform-origin: 5px 0px;
    transition: all 0.2s linear;
    margin-right: 10px;
    cursor: pointer;
  }

  .nav_toggle.open>span {
    transform: rotate(45deg) translate(0px, 3px);
  }

  .nav_toggle.open>span:nth-child(2) {
    display: none;
  }

  .nav_toggle.open>span:nth-child(3) {
    transform: rotate(-45deg) translate(-5px, -2px);
    /* Ajusta la posición si es necesario */
  }
}

