/* Ocultar los botones de autenticación en vista de escritorio */
.auth-buttons {
  display: none;
}

@media (max-width: 700px) {
  .auth-buttons {
    display: contents;
    align-items: center;
    font-size: 15px;
    position: center;
  }

  .auth-btn {
    position: absolute;
    left: 25%;
    margin: 0 0px;
    font-size: 25px;
    background-color: #35363581;
    color: #fffcfc;
    width: 30%;
    text-decoration: none;
    border-radius: 30px;
    transition: background-color 0.3s, color 0.3s;
  }

  .auth-btn:hover {
    background-color: #ff149157;
    color: #fff;
  }

  .social-auth {
    position: abaolute;
    display: flex;
    align-items: center;
    margin-left: 30%;
  }

  .social-btn {
    color: #faf2f2;
    font-size: 20px;
    text-decoration: none;
  }

  .social-btn:hover {
    color: #ff149157;
  }
}